import axios from 'axios'

function emailValidVerify(email) {
    return new Promise((resolve, reject) => {
        let whiteList = ["test-", "@tunekey.test", ".test", "@tk.com", "@tunekey.com", "@tunkey.com"]
        whiteList.forEach(item => {
            if (email.indexOf(item) >= 0) {
                resolve(true)
                return
            }
        })
        let url = "https://emailverification.whoisxmlapi.com/api/v1?apiKey=at_wU22I8A8DpX6v2sXO1XT3hlEBdFtG&emailAddress=" + email
        axios.get(url)
            .then(res => {
                if (res.status) {
                    if (res.status === 200) {
                        let data = res.data
                        if (data) {
                            resolve(data.smtpCheck)
                            return
                        }
                    }
                }
                resolve(false)
            })
            .catch(err => {
                console.error(err);
                reject(err)
            })
    })
    /**
     struct EmailValidationResult: HandyJSON {
    var emailAddress: String = ""
    var formatCheck: Bool = false
    var smtpCheck: Bool = false
    var dnsCheck: Bool = false
    var freeCheck: Bool = false
    var disposableCheck: Bool = false
    var catchAllCheck: Bool = false
}
     */
}

let commons = {
    emailValidVerify
}

export default commons